import {combineReducers} from 'redux';
import {handleActions} from 'redux-actions';
import {browserHistory} from 'react-router';
import {LOGIN, LOGOUT, UPLOAD_IMAGE, UPLOAD_USERINFO} from '../constants';
import {GET_USERINFO} from '../constants';

const saveUsertoStorage = ({userinfo, accessToken, refreshToken}) => {
  localStorage.setItem('userinfo', JSON.stringify(userinfo));
  localStorage.setItem('accessToken', accessToken);
  localStorage.setItem('refreshToken', refreshToken);
};
const removeUserFromStorage = () => {
  localStorage.removeItem('accessToken');
  localStorage.removeItem('refreshToken');
  localStorage.removeItem('userinfo');
};

const user = handleActions({
  [LOGIN]: {
    next: (state = {}, action) => {
      console.log(action);
      saveUsertoStorage(action.payload);
      setTimeout(() => { browserHistory.push('/') }, 100);
      return {...state, ...action.payload};
    },
    throw: (state = {}, action) => {
      console.log(action);
      removeUserFromStorage();
      return {error: action.payload};
    }
  },
  [LOGOUT]: (state = {}, action) => {
      console.log('do logout', action);
      removeUserFromStorage();
      browserHistory.push('/login');
      return {};
    },
  [UPLOAD_IMAGE]: {
    next: (state = {}, action) => {
      const userinfo = {...state.userinfo, image: action.payload};
      localStorage.setItem('userinfo', JSON.stringify(userinfo));
      return {...state, userinfo};
    },
    fail: (state = {}, action) => { console.log(action); return state }
  },
  [UPLOAD_USERINFO]: {
    next: (state = {}, action) => {
      console.log(action);
      return {...state, userinfo: {...state.userinfo, ...action.payload}};
    },
    fail: (state = {}, action) => { console.log(action); return state }
  },
  [GET_USERINFO]: {
    next: (state = {}, action) => {
      console.log(action);
      return {...state, userinfo: {...state.userinfo, ...action.payload}};
    },
    fail: (state = {}, action) => { console.log(action); return state }
  }
}, {});

export default combineReducers({
  user
});
